
*{
  box-sizing: border-box;
  margin: 0;
  padding:0;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size: 2rem;
}

h1{
  font-size: 3rem;
}

h2{
  font-size: 3.5rem;
}

.App{
  width: 100%;
  height: 100vh;
  position: relative;
  color: aliceblue;
}

.App:before{
  content: '';
  background: url('./assets/background.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.App.Search{
  text-align: center;
  padding: 1rem;
}

.App input{
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255,255,0.8);
  background:rgba(255, 255,255,0.8);
}

.instructions{
  text-align: center;
  color: black;
}

.container{
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 80%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(0,0,0,0.4);
}